<template>
  <div class="UpdateName">
    <el-form
         ref="userForm"
        :model="userForm" 
        :rules="rules" 
        status-icon
        label-width="120px"
      >
    <el-form-item label="ÂΩìÂâçÁî®Êà∑ÂêçÔºö" >
      <span class="code">
          {{userInfo.user}}
        </span> 
    </el-form-item>
    <el-form-item label="Êñ∞Áî®Êà∑ÂêçÔºö" prop="user">
      <el-input v-model="userForm.user"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary"  @click="submit('userForm')" plain>Á°ÆËÆ§‰øÆÊîπ</el-button>
    </el-form-item>
  </el-form>
  </div>
</template>
<script>
import { mapState,mapMutations } from "vuex";
export default {
  data() {
    return {
      userForm: {
        user: "",
      },
      rules:{
           user:[
              {
                  validator:(rule,value,cb)=>{
                      //ÈïøÂ∫¶Ê£ÄÈ™å
                      if(value.length<2 || value.length>8){
                          cb(new Error("ËØ∑ËæìÂÖ•2~8‰ΩçÁöÑÁî®Êà∑Âêç"))
                      }
                      //ËßÑÂàôÈ™åËØÅ
                      if(!/^[\w\u4e00-\u9fa5\u0800-\u4e00\uac00-\ud7ff]+$/.test(value)){
                          cb(new Error("Âè™ÂÖÅËÆ∏Â≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ_„ÄÅ‰∏≠/Êó•/Èü©Êñá"))
                      }
                      //‰∏çËÉΩÂíåÂéüÊù•ÁöÑ‰∏ÄÊ†∑
                      if(value === this.userInfo.user){
                          cb(new Error("Áî®Êà∑ÂêçÊ≤°ÊúâÂèòÂåñüòí"))
                      }
                      cb()
                  }
              }
          ],
      }
    };
  },
  computed: {
    ...mapState(["userInfo"]),
  },
  methods: {
      ...mapMutations(['loginSuccess']),
      submit(){
          this.$refs['userForm'].validate(async v=>{
              if(v){
                  let {data} = await this.$axios({
                      method:"POST",
                      url:"/personal/user",
                      data:{
                          user:this.userForm.user
                      }
                  })
                  //‰øÆÊîπÂ§±Ë¥•
                  if(data.code){
                      return this.$message.error(data.msg)
                  }
                  //‰øÆÊîπÊàêÂäü
                  this.$message.success("Áî®Êà∑Âêç‰øÆÊîπÊàêÂäü")//ÊèêÁ§∫
                  this.loginSuccess(data.data)//vuexÊõ¥Êñ∞
                  this.$refs['userForm'].resetFields()//Ë°®ÂçïÈáçÁΩÆ
              }else{
                  return false
              }
          })
      }
  },
};
</script>

<style lang="less" scoped>
.UpdateName{
    font-family: Quicksand;
  span.code{
      color: #bd4147;
      background-color: rgba(27,31,35,.05);
      font-weight: 700;
  }
}

</style>