<template>
  <div class="UpdatePass">
    <el-form
      ref="passForm"
      :model="passForm"
      :rules="rules"
      status-icon
      label-width="120px"
    >
      <el-form-item label="ÂéüÂØÜÁ†ÅÔºö" prop="pass">
        <el-input type="password" v-model="passForm.oldPass"></el-input>
      </el-form-item>
      <el-form-item label="Êñ∞ÂØÜÁ†ÅÔºö" prop="pass">
        <el-input type="password" v-model="passForm.pass"></el-input>
      </el-form-item>
      <el-form-item label="Á°ÆËÆ§Êñ∞ÂØÜÁ†ÅÔºö" prop="pass2">
        <el-input type="password" v-model="passForm.pass2"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit('passForm')" plain
          >Á°ÆËÆ§‰øÆÊîπ</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
export default {
  data() {
    return {
      passForm: {
        oldPass: "",
        pass: "",
        pass2: "",
      },
      rules: {
        pass: [
          {
            validator(rule, value, cb) {
              //ÈïøÂ∫¶È™åËØÅ
              if (value.length < 6 || value.length > 18) {
                cb(new Error("ËØ∑ËæìÂÖ•6~18‰ΩçÁöÑÂØÜÁ†Å"));
              }
              //ËßÑÂàôÈ™åËØÅ
              if (!/^[\w!\[\]{}\/\\:"|?>¬∑`<@#$%^&*\(\)_+=~\-]+$/.test(value)) {
                cb(new Error("ËØ∑ËæìÂÖ•ÂêàÊ≥ïÁöÑËã±ÊñáÂ≠óÁ¨¶"));
              }
              cb();
            },
          },
        ],
        pass2: [
          {
            validator: (rule, value, cb) => {
              if (this.passForm.pass !== value) {
                cb(new Error("‰∏§Ê¨°ËæìÂÖ•ÂØÜÁ†Å‰∏ç‰∏ÄËá¥"));
              }
              cb();
            },
          },
        ],
      },
    };
  },
  methods: {
      ...mapMutations(['loginOut']),
    submit() {
        this.$refs.passForm.validate(async v=>{
            if(v){
                let {data} = await this.$axios({
                    method:"POST",
                    url:"/personal/pass",
                    data:{
                        oldPass:this.passForm.oldPass,
                        pass:this.passForm.pass
                    }
                })
                //‰øÆÊîπÊàêÂäü
                if(data.code){
                    return this.$message.error(data.msg)
                }
                //‰øÆÊîπÊàêÂäü
                this.$message.success("ÂØÜÁ†Å‰øÆÊîπÊàêÂäü,ËØ∑ÈáçÊñ∞ÁôªÂΩï")//ÊèêÁ§∫
                this.$refs["passForm"].resetFields()//Ê∏ÖÁ©∫Ë°®Âçï
                this.loginOut()//vuexÊ∏ÖÁêÜ
                this.$router.push("/")//ÂõûÂà∞È¶ñÈ°µ
            }else{
                return false
            }
        })
    },
  },
};
</script>

