<template>
  <div id="UpdatePhoto">
      <el-alert
      title="‰∏ä‰º†Â§¥ÂÉè"
      description="ÁÇπÂáª‰∏ãÊñπ + Âè∑ Âç≥ÂèØÊõ¥Êñ∞Â§¥ÂÉè"
      type="warning"
      :closable='false'
      show-icon>    
      </el-alert>
    <el-upload
      class="avatar-uploader"
      :with-credentials="true"
      :action="baseURL+'/personal/photo'"
      :show-file-list="false"

      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload"
    >
      <img v-if="imageUrl" :src="imageUrl" class="avatar" />
      <i v-else class="el-icon-plus avatar-uploader-icon "></i>
    </el-upload>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
export default {
  data() {
    return {
      imageUrl: "",
    };
  },
  methods: {
    ...mapMutations(['loginSuccess']),
    handleAvatarSuccess(res, file) {
        /* ‰∏ä‰º†Â§¥ÂÉèuiÁªÑ‰ª∂ÈªòËÆ§ÂèëÈÄÅËØ∑Ê±Ç‰∏çÊê∫Â∏¶cookie ÊâÄ‰ª•ËØ∑Ê±ÇÂèëÈÄÅÂà∞ÂêéÂè∞‰πãÂêé
            Áî±‰∫éËØ∑Ê±ÇÂπ∂Êú™Êê∫Â∏¶cookie ÊâÄ‰ª•ÂêéÂè∞‰ºöËÆ§‰∏∫ÂÖ∂Âπ∂Êú™ÁôªÂΩï ÊâÄ‰ª•‰ºöË¢´Èâ¥ÊùÉ
            Ë∑ØÁî±Êã¶Êà™ ÂèëÈÄÅÁªôÂâçÁ´ØÁöÑ‰ø°ÊÅØ‰∏∫Áî®Êà∑Êú™ÁôªÂΩï
        */
       if(res.code){
           return this.$message.error("‰∏ä‰º†Â§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï")
       }
        //ÊòæÁ§∫Áº©Áï•Âõæ
        this.imageUrl = URL.createObjectURL(file.raw)
        //Êõ¥Êñ∞vuex
        this.loginSuccess(res.data)
    },
    beforeAvatarUpload(file) {
      console.log(file);
      //console.log("this.imageUrl",this.imageUrl);
      const isJPG = /^image\/(jpeg|png)$/.test(file.type);
      const isLt2M = file.size / 1024 / 1024 < 0.5;

      if (!isJPG) {
        this.$message.error("‰∏ä‰º†Â§¥ÂÉèÂõæÁâáÂè™ËÉΩ‰∏∫JPG|PNGÊ†ºÂºè!");
      }
      if (!isLt2M) {
        this.$message.error("‰∏ä‰º†Â§¥ÂÉèÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá 500KB!");
      }
      return isJPG && isLt2M;
    },
  },
};
</script>

<style lang="less" scoped>
#UpdatePhoto {
  /deep/.el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin-top: 10px;
    .avatar{
        display: block;
        max-width: 178px;
        max-height: 178px;
    }
    .avatar-uploader-icon{
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }
  }
}
</style>